                            style="padding: 0;float: right;margin-top:4px;"
                            @click="addFood('')"
                        ></el-button>
                    </div>
                </el-card>
            </el-row>
            <el-row class="other">
                <el-col :span="8">
                    <el-card :body-style="{ paddingBottom: '0px' }">
                        <img src="../../../assets/images/food/mao.jpg" class="image">
                        <div style="padding: 14px;">
                            <span></span>
                            <div class="bottom clearfix">
