                            </div>
                        </div>
                    </el-card>
                </el-col>
                <el-col :span="8">
                    <el-card :body-style="{ paddingBottom: '0px' }">
                        <img src="../../../assets/images/food/mixian.jpg" class="image">
                        <div style="padding: 14px;">
                            <span></span>
                            <div class="bottom clearfix">
                                <el-button type="text" class="button" @click="addFood('')"></el-button>
                            </div>
                        </div>
                    </el-card>
                </el-col>
                <el-col :span="8">
                    <el-card :body-style="{ paddingBottom: '0px' }">
                        <img src="../../../assets/images/food/rice.jpg" class="image">
                        <div style="padding: 14px;">
                            <span></span>
                            <div class="bottom clearfix">
                                <el-button type="text" class="button" @click="addFood('')"></el-button>
                            </div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>
            <el-row class="other">
                <el-col :span="8">
                    <el-card :body-style="{ paddingBottom: '0px' }">
                        <img src="../../../assets/images/food/jiucDum.jpg" class="image">
                        <div style="padding: 14px;">
                            <span></span>
                            <div class="bottom clearfix">
                                <el-button type="text" class="button" @click="addFood('')"></el-button>
                            </div>
                        </div>
                    </el-card>
                </el-col>
                <el-col :span="8">
                    <el-card :body-style="{ paddingBottom: '0px' }">
                        <img src="../../../assets/images/food/liancDum.jpg" class="image">
                        <div style="padding: 14px;">
                            <span></span>
                            <div class="bottom clearfix">
                                <el-button type="text" class="button" @click="addFood('')"></el-button>
                            </div>
                        </div>
                    </el-card>
                </el-col>
                <el-col :span="8">
                    <el-card :body-style="{ paddingBottom: '0px' }">
                        <img src="../../../assets/images/food/qincDum.jpg" class="image">
                        <div style="padding: 14px;">
                            <span></span>
                            <div class="bottom clearfix">
                                <el-button type="text" class="button" @click="addFood('')"></el-button>
                            </div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>
            <el-row class="other">
                <el-col :span="8">
                    <el-card :body-style="{ paddingBottom: '0px' }">
                        <img src="../../../assets/images/food/xiarDum.jpg" class="image">
                        <div style="padding: 14px;">
                            <span></span>
                            <div class="bottom clearfix">
                                <el-button type="text" class="button" @click="addFood('')"></el-button>
                            </div>
                        </div>
                    </el-card>
                </el-col>
                <el-col :span="8">
                    <el-card :body-style="{ paddingBottom: '0px' }">
                        <img src="../../../assets/images/food/luobDum.jpg" class="image">
                        <div style="padding: 14px;">
                            <span></span>
                            <div class="bottom clearfix">
                                <el-button type="text" class="button" @click="addFood('')"></el-button>
                            </div>
                        </div>
                    </el-card>
                </el-col>
                <el-col :span="8">
                    <el-card :body-style="{ paddingBottom: '0px' }">
                        <img src="../../../assets/images/food/baicDum.jpg" class="image">
                        <div style="padding: 14px;">
                            <span></span>
                            <div class="bottom clearfix">
                                <el-button type="text" class="button" @click="addFood('')"></el-button>
                            </div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>
        </div>
        <div class="total">
            <el-tabs v-model="activeName" @tab-click="handleClick" type="border-card">
                <el-tab-pane label="" name="first" style="padding:0px;">
                    <el-card class="box-card" :body-style="{ padding: '0px' }">
                        <div slot="header" class="clearfix">
                            <span class="releaseSpan"></span>
                        </div>
                        <el-table :data="releaseFoods" style="width:100%;" max-height="400" :empty-text="messageOne" >
                            <el-table-column fixed prop="foodName" label="" width="150" align="center" ></el-table-column>
                            <el-table-column fixed="right" label="" width="150" align="center">
                                <template slot-scope="scope">
                                    <el-button @click.native.prevent="deleteRow(scope.$index, releaseFoods)" type="text" size="small" style="padding:0px"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div>
                            <el-button type="text" class="releaseFood" @click="releaseFood" v-if="notRelease"></el-button>
                            <el-button type="text" class="releaseFood" v-if="released" style="color: red"></el-button>
                        </div>
                    </el-card>
                </el-tab-pane>
                <el-tab-pane label="" name="second" style="padding:0px;">
                   <el-card class="box-card" :body-style="{ padding: '0px' }">
                        <div slot="header" class="clearfix">
                            <span class="releaseSpan"></span>
                            <el-button class="el-icon-refresh refresh" type="text" @click="getCollectDinner"></el-button>
                        </div>
                        <el-table :data="collectFoods" style="width:100%;" max-height="450" :empty-text="messageTwo">
                            <el-table-column fixed prop="foodName" label="" width="150" align="center" style="paddind:16px;"></el-table-column>
                            <el-table-column fixed="right" prop="count" label="" width="150" align="center"></el-table-column>
                        </el-table>
                        <el-button type="text" class="releaseFood" @click="open"></el-button>
                    </el-card>
                </el-tab-pane>
            </el-tabs>
        </div>
        <el-dialog v-if="viewDialog" :visible.sync="viewDialog" width="700px" title="">
            <el-table ref="filterTable" :data="alldata" style="width: 100%" max-height="550" :empty-text="messageThree">
                    <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column  prop="foodName" label=""  width="180"  column-key="foodName" 
                :filters = filtersFood
                :filter-method="filterHandler"></el-table-column>
                <el-table-column prop="name" label="" width="180"></el-table-column>
                <el-table-column fixed="right" label="" width="150" align="center">
                    <template slot-scope="scope">
                        <el-button @click.native.prevent="deleteDinner(scope.$index, alldata)" type="text" size="small" style="padding:0px"></el-button>
                    </template>
                </el-table-column>
            </el-table>   
        </el-dialog>
