<script>
import $axios from "@/plugins/ajax";
export default {
  data() {
    return {
      activeName: "first",
      releaseFoods: [], //
      collectFoods: [], //
      released: false, //
      notRelease: true, //
      messageOne: "", //
      messageTwo: "", //
      messageThree: "", //
      viewDialog: false,
      alldata:[],
      filtersFood:[],
    }
  },
  created() {
      this.init();
  },
  methods: {
      init() {
          $axios.get('/order/init').then(res=>{
      
                  this.getDinner();
              
          })
      },
    handleClick(tab, event) {
        if(tab.$options.propsData.name = 'second'){ //  
            this.getCollectDinner();
        }
    },
    addFood(name) {
        //
      if (this.notRelease) { //
        let isHave = true;
        for (let i = 0; i < this.releaseFoods.length; i++) {
          if (name == "") { // 
            for (let j = 0; j < this.releaseFoods.length; j++) {
              if (this.releaseFoods[j].foodName == "" ||this.releaseFoods[j].foodName == "" ||this.releaseFoods[j].foodName == "") {
                isHave = false;
              }
            }
          } else if (this.releaseFoods[i].foodName == name) {//
            isHave = false;
          }
        }
        //
        if (isHave) {
          if (name == "") {
            this.releaseFoods.push({ foodName: "" });
            this.releaseFoods.push({ foodName: "" });
            this.releaseFoods.push({ foodName: "" });
          } else {
            this.releaseFoods.push({ foodName: name });
          }
        } else {
          this.$message.error("");
        }
      }else{ //
          this.$message.error("")
      }
    },
    //
    deleteRow(index, rows) {
      if(this.notRelease){
          rows.splice(index, 1);
      }else{
          this.$message.error("")
      }
    },
    deleteDinner(index,rows){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            $axios.post("/order/admindelete",{userId:rows[index].email,dinner:rows[index].foodName}).then(res=>{
                if(res.data.status){
                    this.$message.success("")
                    rows.splice(index, 1);
                    this.getCollectDinner();
                }else{
                    this.$message.error(res.data.data)
                }
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
    },
    //
    releaseFood() {
        console.log(this.releaseFoods)
        let foodList = []
      if (this.releaseFoods.length <= 1) {
        this.$message.error("");
      } else {
          console.log(this.releaseFoods)
        foodList = JSON.parse(JSON.stringify(this.releaseFoods))
        console.log(this.releaseFoods)
        for(let i = 0;i < foodList.length; i++) {
            if(foodList[i].foodName == '') {
                foodList.splice(i, 1);
                foodList.push({foodName: ''})
                foodList.push({foodName: ''})
                foodList.push({foodName: ''})
                foodList.push({foodName: ''})
                foodList.push({foodName: ''})
                foodList.push({foodName: ''})
                foodList.push({foodName: ''})
            } else if(foodList[i].foodName == '') {
                foodList.splice(i, 1);
                foodList.push({foodName: ''})
                foodList.push({foodName: ''})
            }
        }
        console.log(this.releaseFoods)
        $axios.post("/order/setdinner", {dinner: foodList}).then(res => {
            if (res.data.status == 2) {
                this.$message.error(res.data.data);
                }else{
                    this.$confirm('', '', {confirmButtonText: '', cancelButtonText: '', type: 'warning'}).then(()=>{
                        this.$message.success("");
                        this.released = true;
                        this.notRelease = false;
                    })
                }
            })
        }
    },
    //
    getDinner() {
      $axios.get("/order/gettodaydinner").then(res => {
          let obj = {}
        if (!res.data.status) {
          this.messageOne = res.data.data;
        } else {
          this.released = true;
          this.notRelease = false;
          for (let i = 0; i < res.data.data.length; i++) {
              if(res.data.data[i].dinner.indexOf("") != -1) {
                  obj[""] = 1
              } else if(res.data.data[i].dinner.indexOf("") != -1) {
                  obj[""] = 1
              } else if(res.data.data[i].dinner.indexOf("") != -1) {
                  obj[""] = 1
              } else {
                  obj[res.data.data[i].dinner] = 1
              }
          }
            for (const i in obj) {
                if (obj.hasOwnProperty(i)) {
                    this.releaseFoods.push({foodName: i})
                    
                }
            }
        }
      });
    },
    //
    getCollectDinner() {
      let collectFoods = [];
      $axios.get("/order/getalluserdinner").then(res => {
        if(!res.data.status){
            this.messageTwo = res.data.data;
        }else{
            let object = {};
            let data = res.data.data;
            let alldata = [];
            for (let i = 0; i < data.length; i++) {
                let foodName = data[i].dinner;
                if(typeof object[foodName] === 'undefined'){
                    object[foodName] = 1;
                } else {
                    object[foodName] = object[foodName] + 1;
                }
                alldata.push({
                    foodName: data[i].dinner,
                    name: data[i].name,
                    email:data[i].email});
            }
            this.alldata = alldata;
            for (const key in object) {
                if (object.hasOwnProperty(key)) {
                    collectFoods.push({foodName: key, count: object[key]});
                }
            }
            this.collectFoods = this.getRealFoodsCount(collectFoods);
        }
      });
    },
    getRealFoodsCount(data){
        var item = {'': 0, '': 0, '': 0, '': 0};
        for (let i = 0; i < data.length; i++) {
            let element = data[i]; 
            if (element.foodName === '') {
                item[''] = item[''] + 2 * element.count;
            } else if (element.foodName === '') {
                item[''] = item[''] + 2 * element.count;
            } else if (element.foodName === '') {
                item[''] = item[''] + element.count;
                item[''] = item[''] + element.count;
            } else if (element.foodName === '') {
                item[''] = item[''] + element.count;
                item[''] = item[''] + element.count;
            } else if (element.foodName === '') {
                item[''] = item[''] + element.count;
                item[''] = item[''] + element.count;
            } else if (element.foodName === '') {
                item[''] = item[''] + element.count;
                item[''] = item[''] + element.count;
            } else if (element.foodName === '') {
                item[''] = item[''] + element.count;
                item[''] = item[''] + element.count;
            } else {
                item[element.foodName] = element.count;
            }
        }
        var tData = [];
        for (const key in item) {
            if (item.hasOwnProperty(key)) {
                if(item[key] != 0) {
                    tData.push({foodName:key, count: item[key]});
                }
            }
        }
        return tData;
    },
    //
    getFiltersFood(){
        $axios.get("/order/getTodayDinner").then( res =>{
            for(let i=0;i<res.data.data.length;i++){
                this.filtersFood.push({
                    text:res.data.data[i].dinner,
                    value:res.data.data[i].dinner
                })
            }
        })
    },
    filterHandler(value, row, column) {
        const property = column['property'];
        return row[property] === value;
    },
    clearFilter(){
        this.$refs.filterTable.clearFilter();
    },
    open(){
        this.viewDialog = true;
    }
  },
  mounted() {
      this.getFiltersFood()
  }
};
</script>
<style scoped>
.info {
  height: 30px;
