  padding: 0;
  float: right;
}
.refresh{
  width: 150px;
  position: absolute;
  right:0;
  margin-top:3px;
  text-align: center;
  padding:  0;
}
.card {
  display: flex;
  flex-direction: column;
}
.image {
  width: 100%;
  height: 237px;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
.total {
  width: 24%;
  margin-left: 4%;
  margin-top: 80px;
  margin-bottom: 1px;
  background-color: #fff;
}
.total .releaseSpan {
  margin-left: 25px;
  color: #909399;
}
.box-card {
  position: relative;
  color: #606266;
  height: 500px;
  width: 100%;
}

.delete {
  position: absolute;
  right: 20px;
}
.releaseFood {
  position: absolute;
  padding: 0px;
  bottom: 15px;
  width: 100%;
  text-align: center;
}
</style>


