/*
create by YOU
*/
<template>
  <div style="max-width: 500px; margin: 50px auto 0; padding: 10px">
    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="" prop="name">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-select v-model="form.sex" style="width: 100%" placeholder="">
          <el-option label="" value="male"></el-option>
          <el-option label="" value="female"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="form.email" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="licenseKey">
        <el-input v-model="form.licenseKey" type="password" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="groupName">
        <el-input v-model="form.groupName" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="remark">
        <el-input v-model="form.remark"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="onReset"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script type="text/babel">
  import $axios from '@/plugins/ajax'
  export default {
    data() {
      return {
        data: {},
        form: {
          name: '',
          sex: '',
          email: '',
          groupName: '',
          licenseKey: '',
          remark: ''
        },
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}],
          sex: [{required: true, message: '', trigger: 'blur'}],
          groupName: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    mounted() {
      this.getData()
    },
    methods: {
      getData() {
        //
        $axios.get('/user/getUserInfo').then(({data}) => {
          if (data.status) {
            this.form = data.data[0]
          }
        })
      },
      onSubmit() {
        $axios.post('/user/changeUserInfo', this.form).then(({data}) => {
          if (data.status) {
            this.$message.success(data.data)
          } else {
            this.$message.error(data.data)
          }
        })
      },
      onReset() {
        this.getData()
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">

</style>



