<template>
    <div>
        <div class="ipTable">
            <el-tabs
                v-model="activeName"
                type="border-card"
                @tab-click="handleModeChange">
                <el-tab-pane label="IP" name="first">
                    <div class="head">
                        <div class="filter">
                            <div class="title">
                                <p></p>
                            </div>
                            <div class="select">
                                <el-input type="text" size="small" placeholder="" prefix-icon="el-icon-search" v-model="searchData.searchIp" clearable></el-input>
                            </div>
                        </div>
                        <div class="filter">
                            <div class="title">
                                <p></p>
                            </div>
                            <div class="select">
                                <el-select v-model='searchData.ifUsed' clearable placeholder="" size="small">
                                    <el-option
                                        v-for="item in options1"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                        <div class="filter">
                            <div class="title">
                                <p></p>
                            </div>
                            <div class="select">
                                <el-select v-model='searchData.searchNetworkSegment' clearable placeholder="" size="small">
                                    <el-option
                                        v-for="item in options2"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </div>
                    <div class="content">
                        <el-table
                            ref="filterTable"
                            :data="tableData2"
                            :border='true'
                            height="700px"
                            align="center"
                            :row-class-name='tableRowClassName'>
                            <el-table-column
                                type="index"
                                width="50"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="networkSegment"
                                label=""
                                width="120"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="ip"
                                label="IP"
                                width="180"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="used"
                                label=""
                                width="100"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="name"
                                label=""
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="remarks"
                                label=""
                                align="center">
                            </el-table-column>
                            <el-table-column
                                fixed="right"
                                label=""
                                width="180"
                                align="center">
                                <template slot-scope="scope">
                                    <el-button 
                                        @click.native.prevent="returnIp(scope.row)"
                                        type="primary"
                                        :disabled="forbidden(scope.row)"
                                        size="small">
                                        
                                    </el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-pagination
                            class="pagination"
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5,10, 50, 100]"
                            :page-size="50"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="total">
                        </el-pagination>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="IP" name="second">
                    <el-tabs v-if="activeName=='second'" v-model="currentSegment">
                        <el-tab-pane v-for="(o, indexs) in segment.length" :key="o" :label="`${segment[indexs]}`" :name="segment[indexs]">
                            <div class="overview">
                                <div v-for="(i,index) in ipData[indexs].length" :key="i">
                                    <el-popover
                                      placement="top-start"
                                      width="200"
                                      trigger="hover">
                                        <div>
                                            <p>IP{{ipData[indexs][index].ip}}</p>
                                            <p v-if="Number(ipData[indexs][index].used)"></p>
                                            <p v-else></p>
                                            <p v-if="Number(ipData[indexs][index].used)">{{ipData[indexs][index].name}}</p>
                                            <p v-else></p>
                                        </div>
                                        <div slot="reference" v-if="!Number(ipData[indexs][index].used)" class="ipbox">
                                            <span class="address">{{String(ipData[indexs][index].ip).split('.')[3]}}</span>
                                        </div>
                                        <div slot="reference" v-else class="ipbox ipbox_used" style="cursor:pointer" @click="returnIp('', ipData[indexs][index].ip)">
                                            <span class="address">{{String(ipData[indexs][index].ip).split('.')[3]}}</span>
                                        </div>
                                    </el-popover>
                                </div>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </el-tab-pane>
                <el-tab-pane label="" name="third" class="networkMeanage">
                    <el-table
                        :data="networkSegmentTable"
                        style="width: 85%"
                        class="networkTable"
                        max-height="400"
                        border>
                        <el-table-column
                            fixed
                            prop="networkSegment"
                            label=""
                            align="center"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            fixed
                            prop="usedCount"
                            label=""
                            align="center"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            fixed
                            prop="notUsedCount"
                            label=""
                            align="center"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            fixed="right"
                            align="center"
                            label="">
                            <template slot-scope="scope">
                                <el-button
                                    @click.native.prevent="deleteRow(scope.$index, networkSegmentTable)"
                                    type="text"
                                    size="small">
                                    
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-button type="primary" round class="increase" @click="addNetworkSegment"></el-button>
                 </el-tab-pane>
            </el-tabs>
        </div>
        <!-- DIalog -->
        <el-dialog
            title=""
            :visible.sync="addNetworkSegmentDialog"
            width="30%">
            <el-form :label-position="labelPosition" :rules="rules" label-width="80px" :model="formLabelAlign">
                <el-form-item prop="addSegment" label="">
                    <el-input placeholder=",  215" v-model="formLabelAlign.addSegment"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="addHandle" v-loading="loading" :disabled="disabled"> </el-button>
                    <el-button @click="addNetworkSegmentDialog = false"> </el-button>
                </el-form-item>
            </el-form>
            </el-dialog>
    </div>
</template>

<script>
import $axios from "@/plugins/ajax";
export default {
     data() {
         let checkSegment = (rule, value, callback) => {
            if(!value) {
                return callback(new Error(''))
            }
            if(value > 255 || value < 0) {
                return callback(new Error('0255'))
            }
            if(!(/^\d+$/.test(value))) {
                return callback(new Error(''))
            }
            callback()
         }
        return {
            netNum: '192.168.',
            currentSegment: '',           // 212
            searchData:{ 
                ifUsed: '',                         // 
                searchIp: '',                       //
                searchNetworkSegment: '',           //
            },
            applyIpAddress:'',                      //ip
            allIpData: [],
            options1: [{
                value: '',
            }, {
                value: '',
            }],
           options2: [],
            size: 50,
            currentPage: 1,
            total: 0,                               //
            activeName: 'first',                    //tab
            applyIpDialog: false,                   //IP   
            editRemarks: '',
            networkSegmentTable: [],
            floateMessage:[],
            addNetworkSegmentDialog: false,         //
            labelPosition: 'right',
            formLabelAlign:{
                addSegment:''
            },
            rules: {
                addSegment: [{required: true, validator: checkSegment}]
            },
            segment: [],
            ipData: [],
            loading: false,
            disabled: false
        }
      },
       methods: {
        //IP
        getAllIpList(){
            let temp = []
            $axios.get("/ipmanage/getallip").then(res =>{
                // console.log(res)
                this.ipData = []
                for(let i =0 ;i<res.data.data.length;i++){
                    temp.push(res.data.data[i])
                    if( (i+1) % 254 === 0) {
                        this.ipData.push(temp)
                        temp = []
                    }
                    this.allIpData.push(
                        {   
                            networkSegment: this.anaSegment(res.data.data[i].ip),
                            ip: res.data.data[i].ip,
                            used: res.data.data[i].used == 0 ? '':'',
                            name: res.data.data[i].name == '' ? '':res.data.data[i].name,
                            remarks: res.data.data[i].remarks == ''? '':res.data.data[i].remarks
                        }
                    ); 
                }
                // this.anaIpPool(res.data.data)
            })
        },
        // IP
        apply(segment, num) {
            this.applyIpAddress = `${this.netNum}${segment}.${num}`
            console.log(segment, num)
            this.applyIpDialog = true
        },
        //
        tableRowClassName({row, rowIndex}) {
            if(row.used === ''){
                return 'error-row';
            } else if(row.used === ''){
                return 'success-row';
            }           
        },
        // disabled
        forbidden(row){
            if(row.used === ''){
                return false;
            }else if(row.used === ''){
                return true;
            }
        },
        //ip
        anaSegment(ip) {
            return  String(ip.split('.')[2]) + '';
        },
        // ip
        anaIpPool() {
            $axios.get(`/ipmanage/getallip`).then(res => {
                for (let i = 0; i < res.data.data.length; i++) {
                    if (String(res.data.data[i].ip).split('.')[2] == 212) {
                        this.segment212.push(res.data.data[i])
                    } else if (String(res.data.data[i].ip).split('.')[2] == 213) {
                        this.segment213.push(res.data.data[i])
                    }
                }
            })
        },
        //IP
        applyIp(row){
            this.applyIpDialog = true;
            this.applyIpAddress = row.ip;
        },
        //
        confirmApplyIp(){
            $axios.post("/ipmanage/applyip",{ip:this.applyIpAddress,remarks:this.editRemarks}).then(res =>{
                if(res.data.status){
                    this.applyIpDialog = false;
                    this.allIpData = [];
                    this.getAllIpList();
                    this.anaIpPool()
                    this.getSegment();
                    this.$message.success('')
                }else{
                    this.$message.error('');
                }
            })
        },
        //IP
        returnIp(row, ip){
            let returnedIp = ''
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() =>{
                if(row == '') {
                    returnedIp = ip
                } else {
                    returnedIp = row.ip
                }
                $axios.post("/ipmanage/returnip",{ip:returnedIp}).then(res => {
                    if(res.data.status){
                        this.$message.success('')
                        this.allIpData = [];
                        this.getAllIpList();
                        this.anaIpPool();
                        this.getSegment();
                    }else{
                        this.$message.error('')
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });       
            })
            
        },
        getSegment() {
            $axios.get(`/ipmanage/getsegment`).then(res => {
                this.segment = []
                this.options2 = []
                for (let i = 0; i < res.data.data.length; i++) {
                    this.segment.push(res.data.data[i].segment)
                    this.options2.push({value:(res.data.data[i].segment+"")})
                }
                this.currentSegment = this.segment[0]
            })
        },
         // table
        getSegmentManage(){
            $axios.get("/ipmanage/getsegment").then(({data}) => {
                this.networkSegmentTable = []
                for(let i=0;i<data.data.length;i++){
                    $axios.post("/ipmanage/getcount",data.data[i]).then( res =>{
                        this.networkSegmentTable.push({
                            networkSegment: data.data[i].segment,
                            usedCount: res.data.data,
                            notUsedCount: (254-res.data.data) 
                            })
                    })
                }
            })
            
        },
         // 
        deleteRow(index, rows) {
            let deleteSegment = rows[index].networkSegment.substring(0,3)
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() =>{
                $axios.post("/ipmanage/deletesegment",{segment:deleteSegment}).then(res => {
                    if(res.data.status){
                        this.$message.success('')
                        rows.splice(index, 1);
                        this.getSegment()
                    }else{
                        this.$message.error('')
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });       
            }) 
        },
        addNetworkSegment(){
            this.addNetworkSegmentDialog = true;
        },
        addHandle(){
            let repeatTime = 0;
            this.loading = true
            this.disabled = true
            let segment = this.formLabelAlign.addSegment
            for(let i=0;i<this.segment.length;i++){
                if(segment == this.segment[i]){
                    repeatTime++
                }
            }
            if(repeatTime == 0){
                $axios.post("/ipmanage/addsegment",{segment:segment}).then(res =>{
                    if(res.data.status){
                        this.$message.success('')
                        this.getSegmentManage();
                        this.getSegment()
                        this.addNetworkSegmentDialog = false;
                    }else{
                        this.$message.error('')
                        this.addNetworkSegmentDialog = false;
                    }
                    this.loading = false
                    this.disabled = false
                })
            }else{
                this.$message.error('')
            }
        },
        getIpData() {
            let temp = []
            $axios.get(`/ipmanage/getallip`).then(res => {
                this.ipData = []
                for (let i = 0; i < res.data.data.length; i++) {
                    temp.push(res.data.data[i])
                    if( (i+1) % 254 === 0) {
                        this.ipData.push(temp)
                        temp = []
                    }
                }
                console.log(this.ipData)
            })
        },
        handleSizeChange(val) {
            this.size = val;
        },
        handleCurrentChange(val) {
            this.currentPage = val;
        },
        handleModeChange() {
            if(this.activeName === 'first') {
                this.allIpData = [];
                this.getAllIpList();
            } else if (this.activeName === 'second') {
                this.getSegment()
                this.getSegmentManage()
            }
        }
    },
    mounted () {
        // this.searchData.searchIp = this.$store.state.data.name
        this.getAllIpList();
        this.anaIpPool();
        this.getSegment();
        this.getSegmentManage()
    },
    computed: {
        //
        tableDataFilter(){
            //
            return this.allIpData.filter( item =>{
                if(! this.searchData.ifUsed){
                    return true;
                }
                if(item.used === this.searchData.ifUsed){
                    return true;
                }
            })
            //
            .filter(item => {
                if(! this.searchData.searchIp){
                    return true;
                }else{
                    return Object.keys(item).some(key => {
                    return String(item[key]).toLowerCase().indexOf(this.searchData.searchIp) > -1
                 })
                 return this.allIpData
                }
            })
            //
            .filter(item => {
                if(!this.searchData.searchNetworkSegment) {
                    return true;
                } 
                if(item.networkSegment === this.searchData.searchNetworkSegment) {
                    return true;
                }
            })
        },
        //allIpData 
        tableData2() {
            this.total = this.tableDataFilter.length;
            let from = (this.currentPage - 1)* this.size;
            let to = this.currentPage * this.size;
            let tableData = [];
            for(; from < to; from++){
                if(this.tableDataFilter[from]){
                    tableData.push(this.tableDataFilter[from]);
                }
            }
            return tableData;
        }
    },
    watch: {
        applyIpDialog: function(){
            if(this.applyIpDialog === false){
                this.editRemarks = '';
            }
        }
    }
}
</script>
 
<style scoped>
.message{
    font-weight: bold;
    margin-bottom: 10px;
    color: #888;
}
.ipTable{
    width: 100%;
    max-width: 960px;
}
.head{
    display: flex;
    flex-direction: row;
    background-color: #f9f9f9;
    margin-bottom: 20px;
}
.filter {
    width: 32%;
    display: flex;
    flex-direction: row;
    margin-left:1%;
}
.select {
    display: flex;
    flex-direction: row;
    margin: auto 0;
}
.title {
    display: block;
}
.el-table .error-row{
    color: #F56C6C;
} 
.el-table .success-row{
    color: #409EFF;
}
.pagination{
    float: right;
    margin-top: 10px;
    margin-bottom: 10px;
}
.overview {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.ipbox {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 8px; 
    width: 50px; 
    height: 50px; 
    background-color:#409EFF;
    border-radius: 10px;
}
.address {
    font-size: large;
    font-weight: bold;
}
.ipbox_used {
    background-color:#E9686B;
    cursor:auto;
}
.networkMeanage{
    height: 500px;
}
.networkTable{
    height: 400px;
}
.increase{
    margin-top: 20px;
    margin-left: 70%
}
</style>



