<template>
    <div>
        <div class="ipTable">
            <el-tabs
                v-model="activeName"
                type="border-card"
                style="width:100%;"
                @tab-click="handleClick">
                <el-tab-pane label="IP" name="first">
                     <el-table
                        :data="myIpTableData"
                        style="height:700px;"
                        max-height="700"
                        :border= 'true'>
                        <el-table-column
                            type="index"
                            width="50"
                            align="center">
                        </el-table-column>
                        <el-table-column
                            prop="networkSegment"
                            label=""
                            width="180"
                            align="center">
                        </el-table-column>
                        <el-table-column
                            prop="ip"
                            label="IP"
                            width="230"
                            align="center">
                        </el-table-column>
                        <el-table-column
                            prop="remarks"
                            label=""
                            align="center">
                        </el-table-column>
                        <el-table-column
                            fixed="right"
                            label=""
                            width="180"
                            align="center">
                            <template slot-scope="scope">
                                <el-button
                                    @click.native.prevent="returnIp(scope.row)"
                                    type="primary"
                                    size="small">
                                    
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-pagination
                        class="pagination"
                        @current-change="handleMyIpCurrentPage"
                        :current-page="myIpCurrentPage"
                        layout="total, prev, pager, next, jumper"
                        :total="myIpTotal">
                    </el-pagination>
                </el-tab-pane>
                <el-tab-pane label="IP" name="second">
                    <div class="head">
                        <div class="filter">
                            <div class="title">
                                <p></p>
                            </div>
                            <div class="select">
                                <el-input type="text" size="small" placeholder="" prefix-icon="el-icon-search" v-model="searchData.searchIp" clearable></el-input>
                            </div>
                        </div>
                        <div class="filter">
                            <div class="title">
                                <p></p>
                            </div>
                            <div class="select">
                                <el-select v-model='searchData.ifUsed' clearable placeholder="" size="small">
                                    <el-option
                                        v-for="item in options1"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                        <div class="filter">
                            <div class="title">
                                <p></p>
                            </div>
                            <div class="select">
                                <el-select v-model='searchData.searchNetworkSegment' clearable placeholder="" size="small">
                                    <el-option
                                        v-for="item in options2"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </div>
                    <div class="content">
                        <el-table
                            ref="filterTable"
                            :data="tableData"
                            style="width:100%"
                            :border='true'
                            height="700px"
                            :row-class-name='tableRowClassName'>
                            <el-table-column
                                type="index"
                                width="50"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="networkSegment"
                                label=""
                                width="180"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="ip"
                                label="IP"
                                width="230"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="used"
                                label=""
                                width="100"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="name"
                                label=""
                                width="100"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="remarks"
                                label=""
                                align="center">
                            </el-table-column>
                            <el-table-column
                                fixed="right"
                                label=""
                                width="180"
                                align="center">
                                <template slot-scope="scope">
                                    <el-button 
                                        @click.native.prevent="applyIp(scope.row)"
                                        :type="type(scope.row)"
                                        :disabled="forbidden(scope.row)"
                                        size="small">
                                        IP
                                    </el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-pagination
                            class="pagination"
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5,10, 50, 100]"
                            :page-size="50"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="total">
                        </el-pagination>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="IP" name="third">
                    <el-tabs v-model="currentSegment">
                        <el-tab-pane v-for="(o, indexs) in segment.length" :key="o" :label="`${segment[indexs]}`" :name="segment[indexs]">
                            <div class="overview">
                                <div v-for="(i,index) in ipData[indexs].length" :key="i">
                                    <el-popover
                                      placement="top-start"
                                      width="200"
                                      trigger="hover">
                                        <div>
                                            <p>IP{{ipData[indexs][index].ip}}</p>
                                            <p v-if="Number(ipData[indexs][index].used)"></p>
                                            <p v-else></p>
                                            <p v-if="Number(ipData[indexs][index].used)">{{ipData[indexs][index].name}}</p>
                                            <p v-else></p>
                                        </div>
                                        <div slot="reference" v-if="!Number(ipData[indexs][index].used)" class="ipbox" @click="apply(segment[indexs], i)">
                                            <span class="address">{{String(ipData[indexs][index].ip).split('.')[3]}}</span>
                                        </div>
                                        <div slot="reference" v-else class="ipbox ipbox_used">
                                            <span class="address">{{String(ipData[indexs][index].ip).split('.')[3]}}</span>
                                        </div>
                                    </el-popover>
                                </div>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </el-tab-pane>
            </el-tabs>
        </div>
        <!-- dialog -->
        <el-dialog
            title="IP"
            :visible.sync='applyIpDialog'
            width="30%"
            center>
            <el-input type="text" size="medium" v-model="editRemarks" style="width:70%"></el-input>
            <span slot="footer" class="dialog-footer">
            <el-button @click="applyIpDialog = false"> </el-button>
            <el-button type="primary" @click="confirmApplyIp"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import $axios from "@/plugins/ajax";
export default {
     data() {
        return {
            netNum: '192.168.',
            currentSegment: '',           // 
            searchData:{ 
                ifUsed: '',// 
                searchIp: '',//
                searchNetworkSegment: '',//
            },
            applyIpAddress:'', //ip
            myIpData: [],
            allIpData: [],
            options1: [{
                value: '',
            }, {
                value: '',
            }],
           options2: [],
            size: 50,
            currentPage: 1,
            total: 0,//
            activeName: 'first',//tab
            applyIpDialog: false,
            editRemarks: '',
            segment: [], // 
            ipData: [], // ip
            myIpTotal:0,
            myIpCurrentPage:1,
        }
      },
       methods: {
        //IP
        getAllIpList(){
            let temp = [] // ip
            $axios.get("/ipmanage/getallip").then(res =>{
                //push
                this.allIpData = [];
                // ipDatapush
                this.ipData = []
                // IP
                for(let i =0 ;i<res.data.data.length;i++){
                    temp.push(res.data.data[i])
                    if( (i+1) % 254 === 0) {
                        this.ipData.push(temp)
                        temp = []
                    }
                    this.allIpData.push(
                        {   
                            networkSegment: this.anaSegment(res.data.data[i].ip),
                            ip: res.data.data[i].ip,
                            used: res.data.data[i].used == 0 ? '':'',
                            name: res.data.data[i].name == '' ? '':res.data.data[i].name,
                            remarks: res.data.data[i].remarks == ''? '':res.data.data[i].remarks
                        }
                    ); 
                }
            })
        },
        // IP
        apply(segment, num) {
            this.applyIpAddress = `${this.netNum}${segment}.${num}`
            this.applyIpDialog = true
        },
        //IP
        getMyIpList(){
            $axios.get("/ipmanage/getmyip").then(res => {
                this.myIpData = [];
                for(let i=0;i<res.data.data.length;i++){
                    this.myIpData.push(
                        {
                            networkSegment: this.anaSegment(res.data.data[i].ip),
                            ip: res.data.data[i].ip,
                            remarks:res.data.data[i].remarks
                        }
                    );
                }
            })
        },
        // 
        getSegment() {
            $axios.get(`/ipmanage/getsegment`).then(res => {
                this.segment = []
                this.options2 = []
                for (let i = 0; i < res.data.data.length; i++) {
                    this.segment.push(res.data.data[i].segment)
                    this.options2.push({value:(this.segment[i]+'')})
                }
                this.currentSegment = this.segment[0]
                
            })
        },
        //
        tableRowClassName({row, rowIndex}) {
            if(row.used === ''){
                return 'error-row';
            } else if(row.used === ''){
                return 'success-row';
            }           
        },
        // disabled
        forbidden(row){
            if(row.used === ''){
                return true;
            }else if(row.used === ''){
                return false;
            }
        },
        //buttontypetype
        type(row) { 
            if(row.used === ''){
                return 'danger';
            }else if(row.used === ""){
                return 'primary';
            }
        },
        //ip
        anaSegment(ip) {
            return  String(ip.split('.')[2]) + '';
        },
        //IP
        applyIp(row){
            this.applyIpDialog = true;
            this.applyIpAddress = row.ip;
        },
        //
        confirmApplyIp(){
            $axios.post("/ipmanage/applyip",{ip:this.applyIpAddress,remarks:this.editRemarks}).then(res =>{
                if(res.data.status){
                    this.getAllIpList();
                    this.$message.success('')
                    this.applyIpDialog = false;
                }else{
                    this.$message.error('');
                }
            })
        },
        //IP
        returnIp(row){
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() =>{
                $axios.post("/ipmanage/returnip",{ip:row.ip}).then(res => {
                    if(res.data.status){
                        this.$message.success('')
                        this.getMyIpList();
                    }else{
                        this.$message.error('')
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });       
            })
            
        },
        handleSizeChange(val) {
            this.size = val;
        },
        handleCurrentChange(val) {
            this.currentPage = val;
        },
        handleMyIpCurrentPage(val){
            this.myIpCurrentPage = val;
        },
        //tabtab
        handleClick(event){
            if(event.name == "first"){
                this.getMyIpList();
            }
            if(event.name == "second"){
                this.getAllIpList();
                this.searchData.searchIp = ''
                this.searchData.searchNetworkSegment = ''
                this.searchData.ifUsed = ''
            }
            if(event.name == "third"){
                this.getAllIpList();
            }
        }
    },
    mounted () {
        // this.searchData.searchIp = this.$store.state.data.name
        this.getAllIpList();
        this.getMyIpList();
        this.getSegment();
    },
    computed: {
        //
        tableDataFilter(){
            //
            return this.allIpData.filter( item =>{
                if(! this.searchData.ifUsed){
                    return true;
                }
                if(item.used === this.searchData.ifUsed){
                    return true;
                }
            })
            //
            .filter(item => {
                if(! this.searchData.searchIp){
                    return true;
                }else{
                    return Object.keys(item).some(key => {
                    return String(item[key]).toLowerCase().indexOf(this.searchData.searchIp) > -1
                 })
                 return this.allIpData
                }
            })
            //
            .filter(item => {
                if(!this.searchData.searchNetworkSegment) {
                    return true;
                } 
                if(item.networkSegment === this.searchData.searchNetworkSegment) {
                    return true;
                }
            })
        },
        //allIpData 
        tableData() {
            this.total = this.tableDataFilter.length;
            let from = (this.currentPage - 1)* this.size;
            let to = this.currentPage * this.size;
            let data = [];
            for(; from < to; from++){
                if(this.tableDataFilter[from]){
                    data.push(this.tableDataFilter[from]);
                }
            }
            return data;
        },
        //myIPData 
        myIpTableData(){
            this.myIpTotal = this.myIpData.length
            let from = (this.myIpCurrentPage - 1)* 10;
            let to = this.myIpCurrentPage *10;
            let data = []
            for(;from < to; from++){
                if(this.myIpData[from])
                data.push(this.myIpData[from])
            }
            return  data
        }
        
    },
    watch: {
        applyIpDialog: function(){
            if(this.applyIpDialog === false){
                this.editRemarks = '';
            }
        }
    }
}
</script>
 
<style scoped>
.ipTable{
    width: 100%;
    max-width: 960px;
}
.head{
    display: flex;
    flex-direction: row;
    background-color: #f9f9f9;
    margin-bottom: 20px;
}
.filter {
    width: 32%;
    display: flex;
    flex-direction: row;
    margin-left:1%;
}
.select {
    display: flex;
    flex-direction: row;
    margin: auto 0;
}
.title {
    display: block;
}
.el-table .error-row{
    color: #F56C6C;
} 
.el-table .success-row{
    color: #409EFF;
}
.pagination{
    float: right;
    margin-top: 10px;
    margin-bottom: 10px;
}
.overview {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.ipbox {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 8px; 
    width: 50px; 
    height: 50px; 
    background-color:#409EFF;
    border-radius: 10px;
    cursor: pointer;
}
.address {
    font-size: large;
    font-weight: bold;
}
.ipbox_used {
    background-color:#E9686B;
    cursor:auto;
}
</style>



